.contenedor {
    max-width: 70rem;
    margin: 0 auto;

    @include m.desktop{
        max-width: 90rem;
    }

    &.bases{
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }
}
a {
    text-decoration: none;
}
img {
    max-width: 100%;
}

.contenedor_pagina-error{
    background: linear-gradient(to right, v.$rojo 0%, lighten(v.$rojo, 30%) 100%) ;
    height: 100%;
.pagina-error{
    background: linear-gradient(to right, v.$negro 0%, lighten(v.$negro, 30%) 100%) ;        
        color: transparent;
        background-clip: text;
        text-align: center;
    }
}


.logo_error{
 margin: 0 auto;
 width: 100rem;
}

.oculto{
    display: none;
}

.div-oculto{
    
        overflow: hidden;
        height: 22rem;

        @include m.tablet{
            height: 28rem;
        }
   
}

.div-anuncio{
    
    z-index: 100;
    width: 100%;

    @include m.tablet{
        position: fixed;
    }
    
}
