@use 'variables' as v;
@use "base/mixins" as m;

h1, h2, h3 {
    font-family: v.$fuente_headings;
    font-weight: 900;
}
h1 {
    font-size: 4rem;
    padding: 1rem;
    color: v.$rojo;
    margin-bottom: 0;
}
h2 {
    margin: 2rem 1rem;
    padding: 1rem;
    color: v.$rojo;
    font-size: 4rem;
    border-bottom: solid v.$negro 1px;

    @include m.tablet{
        margin: 2rem 0;
    }
}
h3 {
    font-size: 2.5rem;
    margin: 1rem 0;
    text-align: center;
}

h4{
    margin: 1rem 0;
    text-align: center;
}

p {
    font-size: 2rem;
    color: v.$gris;
    line-height: 1.8;
}

.descripcion-pagina{
    text-align: center;
}

.tagline{
    font-family: v.$fuente_headings;
    font-weight: 900;
    color: v.$negro;
    font-size: 3rem;
    text-align: center;
}