.navbar .btn {
  padding: 0;
}
@media (min-width: 768px) {
  .navbar .btn {
    padding: 0.375rem 0.75rem;
  }
}
.navbar #botonesH {
  margin-top: 1rem;
}
@media (min-width: 768px) {
  .navbar #botonesH {
    margin: 0;
  }
}

.nav-item {
  list-style: none;
}

.icon-tabler-heart:hover {
  fill: #373535;
  transition: fill 0.15s ease-in-out, fill;
}

#favorito:hover .icon-tabler-heart {
  fill: #373535;
  transition: fill 0.15s ease-in-out, fill;
}

.username:hover {
  color: #e6e6e6 !important;
}

.barra {
  z-index: 100;
  width: 100%;
  align-items: center;
  background-color: #373535;
  box-shadow: 1px 12px 9px -13px rgba(255, 255, 255, 0.6);
  display: grid;
  gap: 0;
  grid-template-columns: repeat(1, 1fr);
}
.barra .barra_idioma {
  background-color: #FFFFFF;
}
.barra .barra_idioma .comunicacion {
  color: #373535;
  text-transform: uppercase;
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem;
  margin: 0.5rem 0;
}
.barra .botones_barra {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0.5rem;
}
.barra .botones_barra .botones_nav {
  display: flex;
  padding: 0.2rem;
}
@media (min-width: 768px) {
  .barra .botones_barra .botones_nav img {
    max-width: 8rem;
  }
}
.barra .botones_barra .botones_nav :hover {
  cursor: pointer;
}
.barra .saludo {
  margin-left: 1rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.barra .saludo p {
  margin: 0;
  color: #373535;
  font-size: 1.2rem;
  font-weight: 500;
  display: flex;
}
@media (min-width: 768px) {
  .barra .saludo p {
    font-size: 1.8rem;
  }
}
.barra .saludo span {
  font-weight: 700;
  background: linear-gradient(to right, #ee3338 0%, #fac0c2 100%);
  color: transparent;
  background-clip: text;
  text-align: center;
  display: flex;
  align-items: center;
}
.barra .saludo .hamburguesa {
  height: 100%;
  display: flex;
  justify-content: center;
}
.barra .saludo .hamburguesa svg {
  width: 1.8rem;
}
.barra .saludo .hamburguesa:hover {
  cursor: pointer;
}
.barra .saludo .hamburguesa:hover ul {
  display: block;
}
.barra .saludo .hamburguesa ul {
  list-style: none;
  display: none;
  position: fixed;
  top: 18%;
}
.barra .saludo .hamburguesa ul li a {
  margin: 0;
}
.barra .contenedor_barra {
  display: flex;
}
.barra .contenedor_barra .barra_logo {
  display: flex;
  align-items: center;
  background-color: #ee3338;
  padding: 0.5rem;
}
.barra .contenedor_barra .barra_logo img {
  max-width: 6em;
}
@media (min-width: 768px) {
  .barra .contenedor_barra .barra_logo img {
    max-width: 8em;
  }
}
.barra .contenedor_barra .contenedor_botones {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
}
.barra .session_barras {
  background-color: #FFFFFF;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: auto;
}
.barra .session_barras .cerrar-session {
  background-color: #d9d9d9;
  padding: 1.5rem 4rem;
  color: #ee3338;
  font-size: 1.6rem;
  display: inline-block;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  display: block;
  width: 100%;
  font-size: 1.2rem;
  padding: 1rem;
  width: auto;
  margin-left: 1rem;
}
@media (min-width: 768px) {
  .barra .session_barras .cerrar-session {
    width: auto;
  }
}
.barra .session_barras .cerrar-session:hover {
  background-color: #bfbfbf;
  cursor: pointer;
}
.barra .session_barras .botones_red {
  display: flex;
  justify-content: space-between;
}
.barra .session_barras .botones_red .picture {
  padding: 0.5rem;
  border-radius: 5rem;
  width: 100%;
}
.barra .session_barras .botones_red .picture img {
  max-width: 2rem;
  max-height: 2rem;
}
@media (min-width: 768px) {
  .barra .session_barras .botones_red .picture img {
    padding: 0.5rem;
    max-width: 3.5rem;
    max-height: 3.5rem;
  }
}
.barra .session_barras .botones_red .picture :hover {
  background-color: #e6e6e6;
  cursor: pointer;
}