@use "base/variables" as v;
@use "base/mixins" as m;

#Havatar{
    width: 2.5rem;
    height: 2.5rem;
}

.navbar{
    z-index: 100;
    box-shadow: 1px 12px 9px -13px rgba(255, 255, 255, 0.6);

    .btn{
        padding: 0;
        @include m.tablet{
            padding: 0.375rem 0.75rem;
        }
    }

    #botonesH{
        margin-top: 1rem;
        @include m.tablet{
            margin: 0;
        }
    }
}

.nav-item{
    list-style: none;
}

.icon-tabler-heart{

    &:hover{
        
        fill: v.$negro;
        transition: fill .15s ease-in-out,fill;
    }
}

#favorito{
    &:hover .icon-tabler-heart{
        fill: v.$negro;
        transition: fill .15s ease-in-out,fill;
            
    }
}

.username{

    &:hover{
        color: darken($color: v.$blanco, $amount: 10%) !important;
    }
}

.barra{
        z-index: 100;
        // position: fixed;
        width: 100%;
        align-items: center;
        background-color: v.$negro;
        box-shadow: 1px 12px 9px -13px rgba(255, 255, 255, 0.6);
     @include m.grid(1,0);
    
     .barra_idioma{
        background-color: v.$blanco;

        .comunicacion{
            color: v.$negro;
            text-transform: uppercase;
            text-align: center;
            font-weight: bold;
            font-size: 1.5rem;
            margin: 0.5rem 0;
        }
     }
     
     .botones_barra{
        display: flex;
        justify-content: flex-start;
        align-items: center;
        padding: 0.5rem;

        .botones_nav{
            display: flex;
            padding: 0.2rem;

            @include m.tablet{
                img{
                    max-width: 8rem;
                }
            }
            

            :hover{                
                cursor: pointer;
            }

        }
       
     }

     .saludo{
        margin-left: 1rem;
        display: flex;
        gap: 0.5rem;
        align-items: center;

        p{
            margin: 0;
             color: v.$negro;
            font-size: 1.2rem;
            font-weight: 500;  
            display: flex;   
            
            @include m.tablet{
                font-size: 1.8rem;
            }
        }

        span{
            font-weight: 700;
            @include m.textoDegradado(v.$rojo);  
            display: flex;    
            align-items: center;      
        }

        .hamburguesa{
            height: 100%;
            display: flex;
            justify-content: center;

            svg{
                width: 1.8rem;
            }


            &:hover{
                cursor: pointer;

                ul{
                    display: block;
                }
            }

            ul{
                list-style: none;
                display:none;
                position: fixed;
                top: 18%;

                li{
                    a{
                        margin: 0;
                    }
                    
                }
            }
        }

    }

    .contenedor_barra{
        display: flex;        

        .barra_logo{
            display: flex;
            align-items: center;
            background-color:  v.$rojo;
            padding: 0.5rem;

            img{
                max-width: 6em;
            }
            
            @include m.tablet{
                img{
                    max-width: 8em;
                }
            }
            
        }

        .contenedor_botones{
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            width: 100%;
        }
    
        
    }

    .session_barras{
        background-color: v.$blanco;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: auto;

        .cerrar-session{
            @include m.boton( darken(v.$blanco, 15%), v.$rojo);
            font-size: 1.2rem;
            padding: 1rem;
            width: auto;
            margin-left: 1rem;
        }

        .botones_red{
            display: flex;
            justify-content: space-between;

            .picture{    
                padding: 0.5rem;
                border-radius: 5rem;
                width: 100%;

                img{                   
                    max-width: 2rem;
                    max-height: 2rem;
                }

                @include m.tablet{

                    img{                        
                        padding: 0.5rem;
                        max-width: 3.5rem;
                        max-height: 3.5rem;
                    }
                }

                :hover{
                    background-color: darken(v.$blanco,10%);
                    cursor: pointer;
                }
            }

        }
    }
          
}
