@use "base/variables" as v;
@use "base/mixins" as m;

#avatar{
 max-width: 20rem !important;
 max-height: 20rem !important;
}

.icon-tabler-fill{
    fill: v.$rojo;
}

.d-grid{


        p{
            font-size: 1.2rem;
        }
    
&.fotosdash{
    grid-template-columns: 1fr 1fr;

    @include m.tablet{
        grid-template-columns: 1fr 1fr 1fr ;
    }

    @include m.desktop{
        grid-template-columns: 1fr 1fr 1fr 1fr 1fr 1fr;
    }
    
    
}

.btnFavorito{

    &:hover .btnF{
        filter: invert(100%);
            transition: filter .15s ease-in-out,filter;
    }

    .btnF{
        &:hover{
            filter: invert(100%);
            transition: filter .15s ease-in-out,filter;
        }
    }
}
}
.swal2-image{
    margin: 0 !important;
}

.foto{
    &:hover{
        cursor: pointer;
    }
}

.dashboard{
    // background-color: v.$grisClaro;
    .admin{
        display: flex;
        flex-direction: column;
        justify-content: center;
        
        .concursante-info{
            margin-top: 2rem;
            display: flex;
            justify-content: space-around;
        }
        
        .aprobado{
            background-color: v.$verde;
            
        }

        .noaprobado{
            background-color: v.$rojo;
            
        }

        .aprobado, .noaprobado{
            color: v.$blanco;
            padding: 1rem;
            border-radius: 10rem;
        }

        p{
            color: v.$negro;
        }
    }

    #foto{
        @include m.boton(v.$verde, v.$blanco);
    }

    .registro{
        @include m.boton(v.$verde, v.$blanco);
    }

    .cuponmas{
        @include m.boton(v.$verde, v.$blanco);
        margin-bottom: 1rem;
    }

    .cuponmenos{
        @include m.boton(v.$rojo, v.$blanco);
    }

    .jugar-descripcion{
        font-size: 2.8rem;
    }

    @include m.tablet{
        display: flex;
        height: 100vh !important; // VH viewport 
        
    }
    .logo{
       max-width: 18rem;
        margin: 0 auto;
        margin-bottom: 2rem;
    }
    .principal{
        flex: 1;         
        display: grid;

        .contenido{
            
            // overflow: scroll;
            // overflow-x: hidden;
            scroll-behavior: smooth;
        }
    }

    .cerrar-session_perfil{
        @include m.boton(v.$rojo, v.$blanco);
        padding: 1rem;
        text-transform: uppercase;
        font-weight: bold;
        font-size: 1.4rem;
        margin-top: 2.5rem;
    }

    .enlaces{
        display: flex;
        gap: 2rem;
    }

   .enlace{
    color: darken(v.$naranja, 10%);
    margin-bottom: 2.5rem;
         
     &:hover{
         color: lighten(v.$naranja, 20%);
     }
   }

   .carrusel{    
    min-height: 17rem;

    @include m.tablet{
        margin-top: 2.5rem;
        max-height: 30rem;
    }
   }

   .contenedor-carrusel{
    // max-height: 30rem;
    margin-bottom: 2.5rem;
}

   .contenedor_anuncios_cupones_tendencia{
       margin-top: 5rem;
   }

   .noticia{
    h3{
        text-align: justify !important;
    }

    h1{
        font-size: 30pt !important;
    }
}

   .contenedor_noticia{
    padding: 1rem;

     @include m.grid(1, 0.5rem);
     border: solid 1px;
    margin-bottom: 2.5rem;
    align-items: center;
    justify-items: center;

    img{
        height: 20rem;
    }

    &.index{
        grid-template-columns: repeat(2,1fr) !important;        
        width: 100% !important;
    }

    .contenedor_info{
            border: dotted 2px;
            padding: 1rem;

            
        }

        .titulo{
            color: v.$rojo;
            font-size: 2rem;
            font-weight: bold;
            text-align: center;
           
        }

    p{
        font-size: 1.2rem;
        color: v.$negro;
        margin: 0;

        span{
            color: v.$rojo;
            font-weight: lighter;
        }
    }
   }

   .cupones{
    display: flex;
    flex-direction: column;
   }

   .banner{
    margin-bottom: 2.5rem;
   }

   .videos{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin-bottom: 2.5rem;

    iframe{
        margin-top: 2.5rem; 
        width: 58rem;
    }
   }
   
   .ver_boton{
    margin: 0 auto 2.5rem;

    img{
        height: auto;
    }

    @include m.tablet{
        max-width: 25rem;
    }
   }

   .anuncios{
    display: flex;
    gap: 1rem;
   }

   .boton-terminos{
    @include m.boton(v.$rojo, v.$blanco);
   }

   .contenedor-terminos{
    margin-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;;
   }

   .parrafo{
    font-family: Arial, Helvetica, sans-serif;
    margin: 1rem 1rem;
    line-height: 115% !important;
    
    &.terminos{
        margin: 1.2rem 0 0 0;
        background-color: v.$blanco;
        padding: 1.2rem;
        border-radius: 1rem;
    }

    &.oculto{
        display: none;
    }
    p{
    color: v.$negro !important;
    font-size: 12pt !important;
    line-height: 115% !important;
    }

    span{
        font-size: 12pt !important;
    line-height: 115% !important;
    }

    .letra-uno{
        font-size: 20rem;
        font-weight: bold;
    }
}

.noticia_todas{
    @include m.grid(2,1rem);
}

.contenedor_tendencias{
    display: grid;
    max-width: 90rem;
    margin: 0 auto;

    @include m.tablet{
        grid-template-columns: 60% 40%;
        gap: 1rem;
    }

    @include m.desktop{
            max-width: 120rem;
            gap: 4rem;
        }
    
}

.contenido_recomendacion{
 display: flex;
 flex-direction: column;
 justify-content: space-around;
 @include m.tablet{
    
 }
}

}